import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { FaFilter } from "react-icons/fa";
import { toast } from "react-toastify";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";

import subjectAPI from "api/common/subject";
import trainingSystemAPI from "api/common/trainingSystem";
import academicAPI from "api/common/school-year";

export default function TableFilter({
  currentFilter,
  handleIdMonHocFilterOnChange,
  handleIdHeDaoTaoFilterOnChange,
  handleIdNamHocFilterOnChange,
  handleFilterOnClick,
}) {
  const { t } = useTranslation();

  const [subjects, setSubjects] = useState([]);
  const [trainingSystems, setTrainingSystems] = useState([]);
  const [academicYears, setAcademicYears] = useState([]);

  useEffect(async () => {
    try {
      const subjectRes = await subjectAPI.getAll();
      setSubjects(subjectRes.contents);

      const trainingSystemRes = await trainingSystemAPI.getAll();
      setTrainingSystems(trainingSystemRes.contents);

      const academicYearRes = await academicAPI.getAll();
      setAcademicYears(academicYearRes.contents);
    } catch (err) {
      toast.error(err.toString());
    }
  }, []);

  return (
    <Form className="d-flex flex-wrap">
      <FormGroup>
        <Label for="idMonHoc">{t("admin.syllabus_list.SUBJECT")}</Label>
        <Input
          type="select"
          value={currentFilter.idMonHoc !== null ? currentFilter.idMonHoc : -1}
          onChange={handleIdMonHocFilterOnChange}
        >
          <option value={-1}>{t("admin.common.ALL")}</option>
          {subjects &&
            subjects.map((item, index) => {
              return (
                <option key={index} value={item.id}>
                  {`${item.tenTiengViet}`}
                </option>
              );
            })}
        </Input>
      </FormGroup>
      &emsp;
      <FormGroup>
        <Label for="idHeDaoTao">
          {t("admin.syllabus_list.TRAINING_SYSTEM")}
        </Label>
        <Input
          type="select"
          value={
            currentFilter.idHeDaoTao !== null
              ? currentFilter.idHeDaoTao
              : -1
          }
          onChange={handleIdHeDaoTaoFilterOnChange}
        >
          <option value={-1}>{t("admin.common.ALL")}</option>
          {trainingSystems &&
            trainingSystems.map((item, index) => {
              return (
                <option key={index} value={item.id}>
                  {item.ten}
                </option>
              );
            })}
        </Input>
      </FormGroup>
      &emsp;
      <FormGroup>
        <Label for="idNamHoc">
          {t("admin.syllabus_list.ACADEMIC_YEAR")}
        </Label>
        <Input
          type="select"
          value={
            currentFilter.idNamHoc !== null
              ? currentFilter.idNamHoc
              : -1
          }
          onChange={handleIdNamHocFilterOnChange}
        >
          <option value={-1}>{t("admin.common.ALL")}</option>
          {academicYears &&
            academicYears.map((item, index) => {
              return (
                <option key={index} value={item.id}>
                  {item.ten}
                </option>
              );
            })}
        </Input>
      </FormGroup>
      &emsp;
      <FormGroup className="align-self-end">
        <Button onClick={handleFilterOnClick}>
          <FaFilter /> {t("admin.common.FILTER")}
        </Button>
      </FormGroup>
    </Form>
  );
}
